<template>
  <div id="app">
    <h1>Random Commander Generator</h1>
    <div v-if="loading" class="loading">Loading...</div>

    <div v-else>
      <div class="settings">
        <label for="commander-count">Number of Commanders:</label>
        <input type="number" id="commander-count" v-model.number="commanderCount" min="1" max="10" class="input" />

        <div class="colors">
          <label class="checkbox"><input type="checkbox" v-model="selectedColors.u" /> Blue </label>
          <label class="checkbox"><input type="checkbox" v-model="selectedColors.b" /> Black </label>
          <label class="checkbox"><input type="checkbox" v-model="selectedColors.w" /> White </label>
          <label class="checkbox"><input type="checkbox" v-model="selectedColors.r" /> Red </label>
          <label class="checkbox"><input type="checkbox" v-model="selectedColors.g" /> Green </label>
          <label class="checkbox"><input type="checkbox" v-model="selectedColors.c" /> Colorless </label>
        </div>
        <button @click="fetchCommanders" class="btn">Generate Commanders</button>
      </div>

      <div class="commanders" v-if="commanders.length > 0">
        <div v-for="commander in commanders" :key="commander.id" class="commander-card">
          <a :href="commander.scryfall_uri" target="_blank" class="commander-link">
            <div class="commander-img-wrapper" @mouseover="onHover(commander)" @mouseleave="onLeave(commander)">
              <img :src="getImageUrl(commander)" alt="commander image" class="commander-img">
            </div>

            <div class="commander-info">
              <h2>{{ commander.name }}</h2>
              <p>{{ commander.type_line }}</p>
            </div>

            <div v-if="commander.background" class="background">
              <h3>Background:</h3>
              <p>{{ commander.background.name }}</p>
              <img :src="getImageUrl(commander.background)" alt="background image" class="commander-img">
            </div>
          </a>
        </div>       
      </div>

      <div v-if="commanders.length > 0" class="generate-image-button-container">
        <button @click="generateImage" class="generate-image-button">
          Generate Image of Commanders
        </button>
      </div>

    </div>

    <canvas ref="canvas" style="display: none;"></canvas>
  </div>
</template>


<script>
export default {
  data() {
    return {
      commanders: [],
      loading: false,
      commanderCount: 5, 
      selectedColors: {
        u: false,
        b: false,
        w: false,
        r: false,
        g: false,
        c: false
      },
      lastApiCallTime: 0, 
    };
  },
  methods: {
     onHover(commander) {
        if(commander.card_faces && commander.card_faces.length > 1) {
            commander.isHovered = true;
        }
    },
    onLeave(commander) {
      commander.isHovered = false;
    },
    async ensureDelay(ms) {
      const currentTime = Date.now();
      const timeSinceLastCall = currentTime - this.lastApiCallTime;
      const remainingTime = Math.max(0, ms - timeSinceLastCall);

      if (remainingTime > 0) {
        await new Promise(resolve => setTimeout(resolve, remainingTime)); 
      }

      // Update the last API call time
      this.lastApiCallTime = Date.now();
    },

    async fetchCommanders() {
      this.loading = true;
      try {
        let colorQuery = '';
        const selectedColorKeys = Object.entries(this.selectedColors)
          .filter(([color, selected]) => selected) 
          .map(([color]) => color) 
          .join(''); 

        if (selectedColorKeys) {
          colorQuery = `%20id<=${selectedColorKeys}`;  
        }

        const commanders = [];
        for (let i = 0; i < this.commanderCount; i++) {
          const commander = await this.fetchRandomCommander(colorQuery);
          commanders.push(commander);
        }

        const partnerCount = commanders.filter(commander =>
          commander.keywords && commander.keywords.includes("Partner") && !commander.keywords.includes("Partner with")
        ).length;

        if (partnerCount === 1) {
          const randomPartner = await this.fetchRandomPartner();
          commanders.push(randomPartner);
        }

        const backgroundCount = commanders.filter(commander => commander.keywords && commander.keywords.includes("Choose a background")).length;        
        if(backgroundCount > 0) {            
          const randomBackground = await this.fetchRandomBackground();
          commanders.push(randomBackground);
        }

        this.commanders = commanders;
        this.loading = false;
      } catch (error) {
        console.error("Error fetching commanders:", error);
        this.loading = false;
      }
    },

    async fetchFromUri(uri) {
      await this.ensureDelay(75);
      const response = await fetch(uri);
      const data = await response.json();
      return data;
    },
    async fetchRandomCommander(colorQuery) {
      return this.fetchFromUri(`https://api.scryfall.com/cards/random?q=is:commander%20-t:background%20game:paper${colorQuery}`);
    },
    async fetchRandomPartner() {
      return this.fetchFromUri('https://api.scryfall.com/cards/random?q=o:/^partner$/%20game:paper');
    },
    async fetchRandomBackground() {
      return this.fetchFromUri('https://api.scryfall.com/cards/random?q=t:background%20game:paper');
    },

    getImageUrl(commander) {
        if (commander.image_uris?.normal) 
            return commander.image_uris.normal;

        if (commander.card_faces && commander.card_faces.length > 1) {
            return commander.isHovered
                ? commander.card_faces[1].image_uris.normal
                : commander.card_faces[0].image_uris.normal;
        }
        return 'https://via.placeholder.com/400x600';
    },
    
    async generateImage() {
        const canvas = this.$refs.canvas;
        const ctx = canvas.getContext("2d");

        const imagesPerRow = 3;
        const commanderCount = this.commanders.length;
        const rows = Math.ceil(commanderCount / imagesPerRow); 
        const width = 400;
        const height = 600;
        canvas.width = imagesPerRow * width; 
        canvas.height = rows * height; 

        let completed = 0;

        const imageElements = this.$el.querySelectorAll('.commander-img');

        const imagePromises = Array.from(imageElements).map((img, index) => {
            const x = (index % imagesPerRow) * width;
            const y = Math.floor(index / imagesPerRow) * height;

            img.crossOrigin = 'Anonymous'; 
           
            const onLoad = new Promise((resolve) => {
                if(img.complete) {
                    resolve();
                } else {
                    img.onload = () => resolve();
                }
            });

            onLoad.then(() => {
                ctx.drawImage(img, x, y, width, height); // Draw the image on the canvas
                // completed++;

                // if (completed === commanderCount) {
                //     this.downloadImage(canvas); // Once all images are drawn, download the image
                // }
            });

            return onLoad;
        });

        // Wait for all images to be loaded and drawn
        await Promise.all(imagePromises);
        
        this.downloadImage(canvas);
        
        
        
        // const imagePromises = this.commanders.map(async (commander, index) => {

        //     const imageUrl = this.getImageUrl(commander);
            
        //     // Fetch image as Blob to avoid tainting canvas
        //     let headers = new Headers();
        //     headers.append("Access-Control-Allow-Origin", "*");

        //     const response = await fetch(imageUrl, { headers: headers });
        //     const blob = await response.blob();
            
        //     // Create object URL for the image
        //     const imgUrl = URL.createObjectURL(blob);
            
        //     const x = (index % imagesPerRow) * width;
        //     const y = Math.floor(index / imagesPerRow) * height;

        //     const img = new Image();
        //     img.src = imgUrl;
        //     img.onload = () => {
        //         ctx.drawImage(img, x, y, width, height); 
        //         completed++;
        //         if (completed === commanderCount) {
        //             this.downloadImage(canvas);
        //         }
        //     };
        // });

        // await Promise.all(imagePromises);
    },

    downloadImage(canvas) {
        const link = document.createElement('a');
        link.href = canvas.toDataURL(); 
        link.download = 'commanders_image.png'; 
        link.click(); 
    }
  },
};
</script>

<style scoped>
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
  color: #333;
}

#app {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  text-align: center;
  font-size: 2.5em;
  margin-bottom: 30px;
  color: #333;
}

.loading {
  text-align: center;
  font-size: 1.5em;
  color: #007BFF;
}

.settings {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 40px;
}

.settings label {
  font-size: 1.1em;
  margin-right: 10px;
}

.input {
  font-size: 1.1em;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  max-width: 120px;
  margin-top: 10px;
  transition: border-color 0.3s ease;
}

.input:focus {
  border-color: #007BFF;
  outline: none;
}

.colors {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  margin-top: 15px;
}

.checkbox input[type="checkbox"] {
  margin-right: 8px;
}

.checkbox {
  font-size: 1.1em;
  display: inline-block;
  margin-right: 15px;
}

.checkbox input[type="checkbox"]:checked + span {
  color: #007BFF;
}

.btn {
  background-color: #007BFF;
  color: white;
  border: none;
  padding: 12px 20px;
  font-size: 1.1em;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-top: 20px;
}

.btn:hover {
  background-color: #0056b3;
}

.commanders {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
}

.commander-card {
  background-color: white;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  transition: transform 0.2s ease;
}

.commander-card:hover {
  transform: scale(1.05);
}

.commander-card a {
  text-decoration: none;
  color: inherit;
}

.commander-info {
  margin-top: 15px;
}

.commander-card h2 {
  font-size: 1.5em;
  color: #333;
  margin-bottom: 5px;
}

.commander-card p {
  font-size: 1em;
  color: #666;
  margin-bottom: 10px;
}

.commander-img-wrapper {
  position: relative;
  display: inline-block;
}

.commander-img {
  width: 100%;
  height: auto;
  border-radius: 8px;
  transition: visibility 0.3s ease;
}

.commander-img.second-face {
  visibility: hidden;
}

.commander-img-wrapper:hover .commander-img.second-face {
  visibility: visible;
}


.partner, .background {
  margin-top: 20px;
}

.partner img, .background img {
  width: 50%;
  margin-top: 10px;
}

@media (max-width: 768px) {
  .settings {
    padding: 15px;
  }

  .btn {
    font-size: 1em;
    padding: 8px 16px;
  }

  .commanders {
    grid-template-columns: 1fr 1fr;
  }

  .commander-card {
    padding: 12px;
  }

  .commander-img {
    width: 100%;
  }
}

@media (max-width: 480px) {
  .commanders {
    grid-template-columns: 1fr;
  }

  .settings input[type="number"] {
    width: 70px;
  }
}


.generate-image-button-container {
  text-align: center;
  margin-top: 50px;
}

.generate-image-button {
  padding: 15px 30px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s;
}

.generate-image-button:hover {
  background-color: #45a049;
}
</style>